@import 'checkbox';

.App{
    background-color: rgb(220, 70, 215);
    color:#21f34b;
    max-width:50vw;
    min-width:372px;
    border-radius:15px;
    margin:auto;
    padding:0.5rem;
    &__header{
        text-align: center;
    }
    &__footer{
        display: flex;
        flex-direction: row;
        background-color:white;
        // border: 0.1rem solid black;
        align-items: center;
    }
    &__footer > *{
        margin-left:1rem;
    }
    &__main,&__footer{
        margin-top:0.2rem;
    }
}
.todoForm{
    display:flex;
    background-color:white;
    // border: 0.1rem solid black;
}
.todoForm > input{
    border: 0;
    box-sizing:content-box;
    width:100%;
    font-size:22px;
}
.todoForm > input:focus{
    outline: none !important;
    border:1px solid rgb(33, 243, 75);
}
.todoForm > button{
    border: 0;
    background-color: #21f34b;
}
.filter-link{
    background: none!important;
    border: none;
    padding: 0!important;
    /*optional*/
    font-family: arial, sans-serif;
    /*input has OS specific font-family*/
    color: rgb(41, 153, 0);
    text-decoration: underline;
    cursor: pointer;
    &--active{
        color: #21f34b;;
    }
}
ul{
    padding-left:0;
}
li{
    list-style: none;
    border: 0.1rem solid #21f34b;
    margin-top:0.2rem;
    background-color:white;
}